import { FC, useState } from 'react'
import styled, { css, useTheme } from 'styled-components'

import { Box } from '../Box'
import { Text } from '../Text'
import { ActionListItem, List } from './List'

import { MarginProps } from '../utils/space'
import { resolveToThemeColor } from '../ThemeProvider/utils/colourMap'
import { faChevronDown } from '@awesome.me/kit-46ca99185c/icons/classic/regular'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { IconContainer } from '../sharedStyles/shared.styles'

export type ActionDropdownProps = {
  id?: string
  className?: string
  /** label displayed above the dropdown  */
  label?: string
  /** value */
  value: ActionListItem
  /** list of items for the dropdown list */
  list: ActionListItem[]
  /** onSelect handler */
  onSelect: (action: ActionListItem) => void
} & MarginProps

export const ActionDropdown: FC<ActionDropdownProps> = ({
  id,
  className = '',
  label,
  value,
  list,
  onSelect,
  ...marginProps
}) => {
  const [open, setOpen] = useState(false)

  const theme = useTheme()

  const resolvedBgColor = resolveToThemeColor(
    value.bgColor ?? 'color.background.100',
    theme,
  )
  const resolvedTextColor = resolveToThemeColor(
    value.textColor ?? 'color.text.base',
    theme,
  )

  return (
    <Container
      id={id}
      className={className}
      onClick={() => setOpen((currentOpen) => !currentOpen)}
      {...marginProps}
    >
      {label && (
        <Text tag="label" color="sesame" typo="label">
          {label}
        </Text>
      )}

      <Label $text={resolvedTextColor} $bg={resolvedBgColor}>
        <SelectedOption>{value.label}</SelectedOption>
        <IconContainer
          $size={20}
          style={{
            rotate: open ? '180deg' : '0deg',
          }}
        >
          <FontAwesomeIcon icon={faChevronDown} color={resolvedTextColor} />
        </IconContainer>
      </Label>
      <OuterContainer open={open}>
        <List options={list} selectOption={onSelect} />
      </OuterContainer>
    </Container>
  )
}

interface ISelected {
  $text: string
  $bg: string
}

const Label = styled.div<ISelected>(
  ({ $text, $bg }) => css`
    color: ${$text};
    background-color: ${$bg};
    position: relative;
    width: 100%;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    border-radius: 100px;
    padding: 16px 16px 14px;
    box-sizing: border-box;
    user-select: none;
  `,
)

const SelectedOption = styled.span`
  font-size: 14px;
  font-weight: bold;
  white-space: nowrap;
  text-overflow: ellipsis;
`

const Container = styled(Box)`
  position: relative;
  display: block;
  width: 210px;
  height: 48px;
  text-align: center;
  cursor: pointer;
  outline: none;
  border-radius: 8px;
`

interface IOpen {
  open: boolean
}

const OuterContainer = styled.div<IOpen>(
  ({ open }) => css`
    position: absolute;
    width: 100%;
    border-radius: 12px;
    margin-top: 8px;
    display: ${open ? 'block' : 'none'};
    max-height: ${open ? '235px' : '48px'};
    background-color: ${({ theme }) => theme.color.surface.base[300]};
    overflow-y: ${open ? 'auto' : 'hidden'};
    z-index: 4;
    transition: all 0.2s ease-in-out;
  `,
)
